import Map from "../components/dashboard/Map.js";
import RealEstate from "../components/dashboard/RealEstate";
import { useState, useEffect } from "react";
import { useSelector } from "react-redux"
// store.js 에서 위경도 목록을 불러와서 props로 전송해서 좌표찍기

const Tracking_map = () => {
    let ChooseLatLng = useSelector((state)=> state.ChooseLatLng);
    const [mapKey, setMapKey] = useState(0);

    useEffect(() => {
        // ChooseLatLng가 변경될 때마다 mapKey를 업데이트하여 Map 컴포넌트를 다시 렌더링
        setMapKey((prevKey) => prevKey + 1);
    }, [ChooseLatLng]);

    return (
        <div style={{ position: 'relative', width: '1155px', height: '900px' }}>
            <Map
                key={mapKey} // mapKey를 key로 설정하여 Map 컴포넌트가 재랜더링되도록 함
                initlatitude={ChooseLatLng.lat}
                initlongitude={ChooseLatLng.lng}
                zoom={14}
            />
            <div style={{ position: 'absolute', top: '0', left: '0', padding: "10px"}}>
                <RealEstate />
            </div>
        </div>
    );
};

export default Tracking_map;